.dashContainer{
  display:grid;
  grid-template-columns: repeat(6,1fr);
  grid-template-rows: repeat(6,1fr);
  height:100vh;
  text-align: center;
}

.cookie{
  grid-column:2;
  grid-row:6;
}

.cake{
  grid-column:5;
  grid-row:5;
  margin-bottom: 100px;
  margin-right:30px;
}

.candies{
  grid-column:3;
  grid-row:6;
  margin-bottom: 50px;
}

.other{
  grid-column:3;
  grid-row:5;
  margin-right:170px;
  margin-top:100px;
}

.pies{
  grid-column:1;
  grid-row:6;
}

.pastries{
  grid-column:4;
  grid-row:5;
  margin-left:50px;
}


.brownies{
  grid-column:5/6;
  grid-row:4/5;
}